<template>
  <div class="fbm_monitor">
    <div>
      <el-form
        :inline="true"
        :model="fbm_monitor_form"
        class="demo-form-inline"
      >
        <el-form-item label="国家">
          <el-select v-model="fbm_monitor_form.country">
            <el-option
              v-for="(item, i) in countries"
              :key="i"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="仓库名">
          <el-select v-model="fbm_monitor_form.warehouse">
            <el-option
              v-for="(item, j) in warehouses"
              :key="j"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_con">
      <FbmMonitorCom @changetab="changeCom">
        <component
          :is="comName"
          v-for="(item, i) in dataOptions"
          :key="i"
          :option="item"
        ></component>
      </FbmMonitorCom>
    </div>
  </div>
</template>

<script>
import FbmMonitorCom from "@/components/orderManagement/FBM/FbmMonitorCom";
import FbmMonitorCard1 from "@/components/orderManagement/FBM/FbmMonitorCard1";
import FbmMonitorCard2 from "@/components/orderManagement/FBM/FbmMonitorCard2";
import FbmMonitorCard3 from "@/components/orderManagement/FBM/FbmMonitorCard3";
export default {
  data() {
    return {
      countries: [
        {
          label: "美国",
          value: "usa"
        },
        {
          label: "中国",
          value: "zh"
        }
      ],
      fbm_monitor_form: {
        country: "zh",
        warehouse: "u1"
      },
      warehouses: ["a", "b", "c"],
      //组件
      comName: "FbmMonitorCard1",
      dataOptions: [
        {
          gys: "专递方式",
          country: "usa",
          timeRange: "2012-23-02-2010-12-20",
          percent: "12%",
          target: "23%",
          delaynum: 213,
          allnum: 123
        },
        {
          gys: "专递方式",
          country: "usa",
          timeRange: "2012-23-02-2010-12-20",
          percent: "12%",
          target: "23%",
          delaynum: 213,
          allnum: 123
        },
        {
          gys: "专递方式",
          country: "usa",
          timeRange: "2012-23-02-2010-12-20",
          percent: "12%",
          target: "23%",
          delaynum: 213,
          allnum: 123
        }
      ],
      list: ["FbmMonitorCard1", "FbmMonitorCard2", "FbmMonitorCard3"]
    };
  },
  methods: {
    search() {
      // 请求
    },
    changeCom(val) {
      this.comName = this.list[val];
    }
  },
  components: {
    FbmMonitorCom,
    FbmMonitorCard1,
    FbmMonitorCard2,
    FbmMonitorCard3
  }
};
</script>

<style scoped>
.table_con {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
</style>



