<template>
  <div class="singleItem">
    <div class="tab_menu">
      <div
        :class="{ active: curIdx === i }"
        @click="changeTab(i, item)"
        v-for="(item, i) in list"
        :key="i"
      >
        {{ item }}
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  data() {
    return {
      curIdx: 0,
      list: ["订单迟发率", "配送超时率", "丢件率"]
    };
  },
  methods: {
    changeTab(i, val) {
      console.log(i, val);
      this.curIdx = i;
      this.$emit("changetab", i);
    }
  }
};
</script>

<style scoped>
.singleItem {
  display: flex;
  flex-direction: row;
  font-size: 16px;
  background-color: #fff;
}
.tab_menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
}
.tab_menu > div {
  text-align: center;
  width: 100%;
  height: 100px;
  border: 1px solid black;
  line-height: 100px;
}
.tab_menu .active {
  background-color: #aaa;
}
</style>



