<template>
  <div class="smallcard">
    <div>
      <p>
        <span>供应商</span><span>{{ option.gys }}</span>
      </p>
      <p>
        <span>国家</span><span>{{ option.country }}</span>
      </p>
      <p>
        <span>时间范围</span><span>{{ option.timeRange }}</span>
      </p>
    </div>
    <div>
      <p>
        <span class="bigPercent">{{ option.percent }}</span
        ><span class="target">目标：{{ option.target }}</span>
      </p>
    </div>
    <div>
      <p>
        <span>超时配送订单</span><span>{{ option.delaynum }}</span>
      </p>
      <p>
        <span>总发货数量</span><span>{{ option.allnum }}</span>
      </p>
      <p class="toOrderDetail"><button>查看订单详情</button></p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["option"]
};
</script>

<style scoped>
.smallcard {
  font-size: 14px;
  width: 350px;
  height: 300px;
  padding: 20px;
  border: 1px solid black;
}
.smallcard p {
  line-height: 2;
}
.smallcard p > span:nth-child(1) {
  width: 120px;
  display: inline-block;
}
.smallcard .bigPercent {
  font-size: 50px;
}
.smallcard .target {
  vertical-align: 7px;
  margin-left: 20px;
}
.toOrderDetail {
  text-align: center;
  margin-top: 5px;
}
.toOrderDetail button {
  background-color: #409eff;
  color: #fff;
}
</style>



